import {
  USER_DATA_PENDING,
  USER_DATA_SUCCESS,
  USER_DATA_FAILED,
  DELETE_USER_DATA,
} from "./constants";

import {
  ADD_WATCHLIST_SUCCESS,
  ADD_WATCHLIST_PENDING,
  ADD_WATCHLIST_FAILED,
  ADD_SYMBOL_PENDING,
  ADD_SYMBOL_FAILED,
  DELETE_WATCHLIST_FAILED,
  DELETE_WATCHLIST_PENDING,
  DELETE_WATCHLIST_SUCCESS,
  ADD_SYMBOL_SUCCESS,
  REMOVE_SYMBOL_FAILED,
  REMOVE_SYMBOL_PENDING,
  REMOVE_SYMBOL_SUCCESS,
} from "../../WebPages/Watchlist/constants";

import {
  createNewWatchlist,
  deleteWatchlist,
  getWatchlist,
  removeWatchlistSymbol,
} from "../../Server Sync Fns/watchlistSync";

import merge from "lodash/merge";
const initUserData = {
  userData: {
    username: "",
    adminType: "none",
    watchlists: [
      {
        name: "default",
        symbols: ["TSLA", "AMZN", "AAPL"],
      },
    ],
    stockCrawler: [],
    events: {
      alerts: [],
      events: [],
    },
    portfolio: {
      account_value: 0,
      active_orders: [],
      owned_stocks: [],
      traded_stocks: [],
      stocks_PnL: 0,
      history: {
        deposits: [],
        withdrawals: [],
        orders: [],
        daily: [],
      },
    },

    Robotrade_portfolio: {
      trading_style: "Aggressive",
      deposit: 0,
      amount_allocated: 0,
      net_value: 0,
      profit_or_loss: 0,
      trade_with_profits: "no",
      orders: [],
      stock_to_trade: [],
      traded_stocks: [],
      history: {
        deposits: [],
        withdrawals: [],
        orders: [],
        daily: [],
      },
    },
  },
  isPending: false,
  error: "",
};

export const userDataObj = (state = initUserData, action = {}) => {
  let userData = state.userData;
  let watchlist;

  switch (action.type) {
    case USER_DATA_PENDING:
      return { ...state, isPending: true };
    case USER_DATA_SUCCESS:
      return { ...state, isPending: false, userData: action.payload };
    case USER_DATA_FAILED:
      return { ...state, error: action.payload };

    case ADD_WATCHLIST_PENDING:
      return { ...state, isPending: true };
    case ADD_WATCHLIST_SUCCESS:
      createNewWatchlist(userData, action.payload);
      return { ...state };
    case ADD_WATCHLIST_FAILED:
      return { ...state, error: action.payload };

    case DELETE_WATCHLIST_PENDING:
      return { ...state, isPending: true };
    case DELETE_WATCHLIST_SUCCESS:
      deleteWatchlist(userData, action.payload);
      return { ...state };
    case DELETE_WATCHLIST_FAILED:
      return { ...state, error: action.payload };

    case ADD_SYMBOL_PENDING:
      return { ...state, isPending: true };
    case ADD_SYMBOL_SUCCESS:
      watchlist = getWatchlist(userData, action.payload.watchlistName);
      watchlist.symbols.push(action.payload.symbol);
      return { ...state };
    case ADD_SYMBOL_FAILED:
      return { ...state, error: action.payload };

    case REMOVE_SYMBOL_PENDING:
      return { ...state, isPending: true };
    case REMOVE_SYMBOL_SUCCESS:
      watchlist = getWatchlist(userData, action.payload.watchlistName);
      removeWatchlistSymbol(watchlist, action.payload.symbol);
      return { ...state };
    case REMOVE_SYMBOL_FAILED:
      return { ...state, error: action.payload };

    case DELETE_USER_DATA:
      console.log("DELETING USER DATA");
      return initUserData;

    default:
      return state;
  }
};
